import Vue from 'vue';
export default Vue.extend({
    props: {
        type: {
            type: String,
            default: 'horizontal'
        },
        dashed: {
            type: Boolean,
            default: false
        }
    },
    render(h) {
        const { type, dashed } = this;
        return h('div', {
            class: {
                divider: true,
                [`divider-${type}`]: true,
                'divider-dashed': !!dashed
            },
            props: this.$attrs
        }, [
            this.$slots.default &&
                h('span', {
                    class: 'divider-inner-text'
                }, this.$slots.default)
        ]);
    }
});



