import Vue from 'vue';
import { PageHeader } from 'components/PageHeader';
export default Vue.extend({
    components: {
        PageHeader
    },
    props: {
        title: String,
        content: String,
        logo: String,
        action: String,
        extraContent: String,
        breadcrumbNameMap: Object,
        tabList: Array,
        tabActiveKey: String
    }
});



